
/*https://material.io/resources/color/#!/?view.left=0&view.right=0&primary.color=00695C&secondary.color=B2DFDB*/
:root {
  --mdc-theme-primary: #00695c;
  --mdc-theme-secondary: #b2dfdb;
  --mdc-theme-background: red;
  --mdc-theme-surface: var(--mdc-theme-secondary);
  --mdc-theme-on-primary: white;
  --mdc-theme-on-secondary: black;
  --mdc-theme-on-surface: red;
  --mdc-theme-text-primary-on-light: black;
  --mdc-theme-text-secondary-on-light: black;
  --mdc-theme-text-hint-on-light: red;
  --mdc-theme-text-disabled-on-light: red;
  --mdc-theme-text-icon-on-light: red;
  --mdc-theme-text-primary-on-dark: white;
  --mdc-theme-text-secondary-on-dark:black;
  --mdc-theme-text-hint-on-dark: red;
  --mdc-theme-text-disabled-on-dark: red;
  --mdc-theme-text-icon-on-dark: red;
}